import { CharStreams, CommonTokenStream } from 'antlr4ts';
import { TodoLangGrammarLexer } from '@/ANTLR/TodoLangGrammarLexer';
import { TodoLangGrammarParser } from '@/ANTLR/TodoLangGrammarParser';
export const createLexer = input => {
  const chars = CharStreams.fromString(input);
  return new TodoLangGrammarLexer(chars);
};
export const getParser = input => {
  const lexer = createLexer(input);
  const tokenStream = new CommonTokenStream(lexer);
  const parser = new TodoLangGrammarParser(tokenStream);
  parser.removeErrorListeners();
  lexer.removeErrorListeners();
  return parser;
};
export const getAST = input => {
  const parser = getParser(input);
  const ast = parser.tsl();
  return ast;
};
